@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0 auto;
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
  font-family: "Open Sans", "Geneva", sans-serif;
  color: #444444;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  object-fit: cover;
  object-position: center;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  width: 100%;
  max-width: 1150px;
  margin: 0 auto;
  padding: 0 30px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 50px;
  }
}
@media (min-width: 1150px) {
  .container {
    padding: 0 52px;
  }
}

.title {
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
  color: #000000;
  margin: 0;
}
@media (min-width: 768px) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}
@media (max-width: 767px) {
  .title--mini {
    font-size: 25px;
  }
}

.first-screen {
  position: relative;
  background-color: #f1f1f1;
}
@media (min-width: 768px) {
  .first-screen::after {
    content: "";
    position: absolute;
    bottom: -5px;
    width: 100%;
    height: 24px;
    background-image: url("../img/svg/zigzag-fill-tablet.svg");
    background-repeat: repeat-x;
  }
}
@media (min-width: 1150px) {
  .first-screen::after {
    bottom: -17px;
    height: 34px;
    background-image: url("../img/svg/zigzag-fill-desktop.svg");
  }
}
.first-screen--home {
  background-color: #f9f9f9;
  background-image: url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: -17px;
}
@media (min-resolution: 2dppx) {
  .first-screen--home {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-size: 320px auto;
  }
}
@media (min-width: 768px) {
  .first-screen--home {
    background-image: url("../img/hero-tablet@1x.jpg");
    background-position-y: -44px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .first-screen--home {
    background-image: url("../img/hero-tablet@2x.jpg");
    background-size: 768px auto;
  }
}
@media (min-width: 1150px) {
  .first-screen--home {
    background-image: url("../img/hero-desktop@1x.jpg");
    background-position-y: -62px;
  }
}
@media (min-width: 1150px) and (min-resolution: 2dppx) {
  .first-screen--home {
    background-image: url("../img/hero-desktop@2x.jpg");
    background-size: 1150px auto;
  }
}

.first-screen--home .first-screen__container {
  display: flex;
  flex-direction: column;
  min-height: 488px;
  background-color: transparent;
}
@media (min-width: 768px) {
  .first-screen--home .first-screen__container {
    min-height: 709px;
  }
}
@media (min-width: 1150px) {
  .first-screen--home .first-screen__container {
    min-height: 687px;
  }
}

.first-screen__title {
  margin: 0 auto;
  padding: 74px 0;
  text-align: center;
}
@media (min-width: 768px) {
  .first-screen__title {
    padding-top: 57px;
    padding-bottom: 81px;
  }
}
@media (min-width: 1150px) {
  .first-screen__title {
    padding-top: 39px;
    padding-bottom: 100px;
  }
}
.first-screen__title--home {
  max-width: 300px;
  padding: 73px 0;
}
@media (min-width: 768px) {
  .first-screen__title--home {
    max-width: 520px;
    padding: 112px 0;
  }
}
@media (min-width: 1150px) {
  .first-screen__title--home {
    max-width: 700px;
    padding: 96px 0;
  }
}

.first-screen--form .first-screen__title {
  padding: 72px 0;
}
@media (min-width: 768px) {
  .first-screen--form .first-screen__title {
    padding: 58px 0 80px;
  }
}
@media (min-width: 1150px) {
  .first-screen--form .first-screen__title {
    padding: 42px 0 100px;
  }
}

.first-screen__buttons {
  margin: auto -30px 0;
}
@media (min-width: 768px) {
  .first-screen__buttons {
    display: grid;
    margin: auto 0 0;
    grid-template-columns: repeat(2, 1fr);
  }
}

.first-screen__buttons-item {
  position: relative;
  display: block;
  padding: 24px 115px 31px 30px;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  color: #ffffff;
  text-decoration: none;
  background-color: #63d1bb;
}
@media (min-width: 768px) {
  .first-screen__buttons-item {
    padding: 26px 115px 40px 36px;
    font-size: 20px;
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .first-screen__buttons-item {
    padding-bottom: 34px;
  }
}
.first-screen__buttons-item:nth-child(2n) {
  background-color: #56c3b2;
}
@media (min-width: 768px) {
  .first-screen__buttons-item:hover, .first-screen__buttons-item:focus {
    outline: none;
    transform: translateY(-5px);
  }
}

@media (min-width: 768px) {
  .first-screen__buttons-item:active svg {
    opacity: 0.3;
  }
}

.first-screen__buttons-item span {
  display: block;
  max-width: 130px;
}

.first-screen__buttons-item svg {
  position: absolute;
  right: 36px;
  bottom: 20px;
  opacity: 0.5;
  fill: #149080;
}
@media (min-width: 768px) {
  .first-screen__buttons-item svg {
    bottom: 39px;
  }
}

.first-screen__buttons-item--toys svg {
  right: 27px;
}
@media (min-width: 768px) {
  .first-screen__buttons-item--toys svg {
    right: 33px;
  }
}

.nav {
  position: relative;
  min-height: 79px;
  padding-top: 79px;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 768px) {
  .nav {
    padding: 0;
    background-color: #f9f9f9;
    border: none;
  }
}
@media (min-width: 1150px) {
  .nav {
    background-color: #ffffff;
  }
}

.nav__logo {
  position: absolute;
  top: 22px;
  left: 50%;
  width: 86px;
  height: 35px;
  padding: 0;
  transform: translateX(-50%);
}
@media (min-width: 768px) and (max-width: 1149px) {
  .nav__logo {
    left: 48px;
    width: 114px;
    height: 46px;
    transform: none;
  }
}
@media (min-width: 1150px) {
  .nav__logo {
    top: 9px;
    width: 149px;
    height: 72px;
  }
}
@media (min-width: 768px) {
  .nav__logo:hover, .nav__logo:focus {
    outline: none;
    opacity: 0.6;
  }
}
@media (min-width: 768px) {
  .nav__logo:active {
    opacity: 0.3;
  }
}

.nav__logo img {
  display: block;
  width: 100%;
  height: 100%;
}

.nav__toggle {
  position: absolute;
  top: 29px;
  right: 31px;
  z-index: 10;
  width: 20px;
  height: 20px;
  padding: 0;
  background-color: transparent;
  background-image: url("../img/svg/menu.svg");
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}

.nav--opened .nav__toggle {
  background-image: url("../img/svg/menu-close.svg");
}

.nav__list-container {
  position: absolute;
  top: auto;
  right: 0;
  left: 0;
  z-index: 10;
  display: none;
  padding: 0;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .nav__list-container {
    position: static;
    display: grid;
    align-items: start;
    background-color: #f1f1f1;
    gap: 20px 0;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1150px) {
  .nav__list-container {
    padding: 0 15px;
    background-color: #ffffff;
    gap: 0 150px;
  }
}

.nav--opened .nav__list-container {
  display: grid;
}

@media (min-width: 768px) {
  .nav--page .nav__list-container {
    background-color: #f1f1f1;
  }
}
@media (min-width: 1150px) {
  .nav--page .nav__list-container {
    background-color: #ffffff;
  }
}

@media (min-width: 768px) {
  .nav__list {
    display: flex;
    flex-wrap: wrap;
  }
}

.nav__item {
  box-shadow: 0 1px 0 #ececec, inset 0 1px 0 #ececec;
}
@media (min-width: 768px) {
  .nav__item {
    box-shadow: none;
  }
}

.nav__link {
  position: relative;
  display: block;
  padding: 30px 30px 25px 75px;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  color: #000000;
  text-decoration: none;
}
@media (min-width: 768px) {
  .nav__link {
    display: inline-block;
    min-height: 87px;
    padding: 33px 49px 30px 82px;
  }
}
@media (min-width: 1150px) {
  .nav__link {
    padding: 33px 37px 30px 84px;
  }
}

.nav__link[href]:hover, .nav__link[href]:focus {
  color: rgba(0, 0, 0, 0.6);
  outline: none;
}
.nav__link[href]:active {
  opacity: 0.3;
}

.nav__link svg {
  position: absolute;
  top: 50%;
  left: 30px;
  transform: translateY(-50%);
  fill: rgba(0, 0, 0, 0.2);
}

.nav__link:hover svg,
.nav__link:focus svg {
  fill: #63d1bb;
}

@media (min-width: 768px) {
  .nav__list--catalog {
    z-index: 1;
    display: grid;
    justify-content: start;
    order: 1;
    padding-left: 51px;
    gap: 36px;
    grid-template-columns: repeat(2, auto);
  }
}
@media (min-width: 1150px) {
  .nav__list--catalog {
    order: 0;
    padding-left: 0;
    gap: 0;
  }
}

@media (min-width: 1150px) {
  .nav__list--catalog .nav__item:nth-child(2n) {
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .nav__list--catalog .nav__link {
    min-height: auto;
    padding: 0;
    font-size: 15px;
    line-height: 24px;
  }
}
@media (min-width: 1150px) {
  .nav__list--catalog .nav__link {
    padding: 33px 35px 30px;
  }
}

@media (min-width: 768px) and (max-width: 1149px) {
  .nav__list--user {
    justify-content: flex-end;
    padding-left: 200px;
    background-color: #ffffff;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    grid-column: 1/-1;
  }
}
@media (min-width: 1150px) {
  .nav__list--user {
    max-width: 400px;
    margin-left: auto;
    border: none;
  }
}

@media (min-width: 768px) {
  .nav__list--user .nav__item:nth-child(2n) {
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
}

@media (min-width: 768px) {
  .nav__list--user .nav__link--search {
    display: flex;
    padding: 33px 41px 30px;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    place-content: center;
  }
}

@media (min-width: 768px) {
  .nav__link--search span {
    display: none;
  }
}

@media (min-width: 1150px) {
  .nav__sale-container {
    background-color: #f1f1f1;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }
}

.nav__sale-wrapper {
  position: relative;
  display: none;
}
@media (min-width: 768px) {
  .nav__sale-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    order: 1;
    max-width: 50%;
    margin: -24px 0 0 auto;
    padding-right: 51px;
  }
}
@media (min-width: 1150px) {
  .nav__sale-wrapper {
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    max-width: 1150px;
    margin: 0 auto;
    padding: 18px 51px;
  }
}

.nav__sale-text {
  margin: 0;
  line-height: 24px;
}
@media (min-width: 768px) {
  .nav__sale-text {
    font-size: 15px;
  }
}

.nav__list--sale {
  display: none;
}
@media (min-width: 1150px) {
  .nav__list--sale {
    display: grid;
    gap: 0 62px;
    grid-template-columns: repeat(2, auto);
  }
}

@media (min-width: 1150px) {
  .nav__list--sale .nav__link {
    min-height: auto;
    padding: 0;
    font-size: 15px;
    line-height: 24px;
  }
}

@media (max-width: 767px) {
  .nojs .nav__list-container {
    position: static;
  }
}

@media (max-width: 767px) {
  .nojs .nav__toggle {
    display: none;
  }
}

.promo__wrapper {
  display: flex;
  flex-direction: column;
  padding-top: 26px;
  padding-bottom: 42px;
  background-image: url("../img/svg/zigzag-line-mobile.svg");
  background-repeat: repeat-x;
  background-position: bottom;
}
@media (min-width: 768px) {
  .promo__wrapper {
    display: grid;
    padding-top: 50px;
    padding-bottom: 69px;
    column-gap: 42px;
    background-image: url("../img/svg/zigzag-line-tablet.svg");
    grid-template-columns: 292px 1fr;
  }
}
@media (min-width: 1150px) {
  .promo__wrapper {
    position: relative;
    min-height: 740px;
    padding-top: 70px;
    padding-bottom: 96px;
    padding-left: 225px;
    column-gap: 58px;
    background-image: url("../img/svg/zigzag-line-desktop.svg");
    background-repeat: no-repeat;
    grid-template-columns: 1fr 525px;
    grid-template-rows: 120px auto auto;
  }
}

.promo__item {
  margin: 0;
  padding: 7px 0 12px;
  line-height: 20px;
  border-bottom: 1px solid #ececec;
}
@media (min-width: 1150px) {
  .promo__item {
    padding: 7px 0 13px;
  }
}

@media (min-width: 768px) {
  .promo__title-container {
    display: flex;
    justify-content: space-between;
    vertical-align: center;
    text-align: center;
    grid-column: 1/-1;
  }
}
@media (min-width: 1150px) {
  .promo__title-container {
    justify-content: flex-start;
    padding-top: 69px;
    grid-row: 1/span 1;
  }
}

.promo__title {
  line-height: 40px;
}

.promo__type {
  display: block;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #63d1bb;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .promo__type {
    max-width: 69px;
    margin-right: 30px;
    line-height: 17px;
  }
}
@media (min-width: 1150px) {
  .promo__type {
    position: absolute;
    top: 138px;
    left: 81px;
    max-width: 100px;
  }
}

.promo__price-container {
  order: 10;
  margin-top: 26px;
}
@media (min-width: 768px) {
  .promo__price-container {
    margin-top: auto;
  }
}
@media (min-width: 1150px) {
  .promo__price-container {
    grid-column: 1/2;
  }
}

.promo__price {
  margin: 0;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  text-align: center;
}
@media (min-width: 768px) {
  .promo__price {
    font-size: 20px;
    line-height: 24px;
  }
}

.promo__button {
  width: 100%;
  margin-top: 25px;
}
@media (min-width: 768px) {
  .promo__button {
    margin-top: 33px;
  }
}

.promo__image {
  margin: 0;
  margin-top: 28px;
}
@media (min-width: 768px) {
  .promo__image {
    margin-top: 55px;
    grid-column: span 1/-1;
    grid-row: 2/span 2;
  }
}
@media (min-width: 1150px) {
  .promo__image {
    z-index: -1;
    width: 525px;
    height: 636px;
    margin-top: 0;
    grid-row: 1/span 3;
  }
}

.promo__image img {
  display: block;
  width: 100%;
  height: 100%;
}

.promo__description p {
  margin: 0;
  margin-top: 16px;
}
@media (min-width: 768px) {
  .promo__description p {
    margin-top: 43px;
    font-size: 17px;
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .promo__description p {
    margin-top: 33px;
  }
}

.promo__list {
  display: grid;
  margin-top: 21px;
  grid-template-columns: 97px 1fr;
}
@media (min-width: 768px) {
  .promo__list {
    margin-top: 39px;
  }
}
@media (min-width: 1150px) {
  .promo__list {
    margin-top: 52px;
  }
}

.button {
  display: block;
  padding: 14px;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  font-family: "Open Sans", "Geneva", sans-serif;
  text-align: center;
  color: #444444;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #ffffff;
  border: 2px #444444 solid;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button {
    font-size: 17px;
  }
}
.button:hover {
  color: #ffffff;
  background-color: #444444;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button:focus {
  color: #ffffff;
  background-color: #444444;
  outline: 2px solid #63d1bb;
}
.button:disabled {
  color: #ececec;
  background-color: #c7c7c7;
  border-color: #c7c7c7;
  pointer-events: none;
}
.button--disabled {
  color: #ececec;
  background-color: #c7c7c7;
  border-color: #c7c7c7;
  pointer-events: none;
}

.about__wrapper {
  padding-top: 31px;
  padding-bottom: 15px;
}
@media (min-width: 768px) {
  .about__wrapper {
    padding-top: 56px;
    padding-bottom: 28px;
  }
}
@media (min-width: 1150px) {
  .about__wrapper {
    padding: 67px 228px 32px 225px;
  }
}

.about__list {
  padding-top: 27px;
}
@media (min-width: 768px) {
  .about__list {
    padding-top: 42px;
    column-count: 2;
    column-gap: 0;
  }
}

.about__item {
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  color: #000000;
  margin-bottom: 28px;
  padding-top: 55px;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .about__item {
    font-size: 20px;
    line-height: 22px;
    max-width: 300px;
    margin-bottom: 54px;
    padding-top: 56px;
  }
}
@media (min-width: 1150px) {
  .about__item {
    padding-top: 56px;
  }
}
.about__item--ecology {
